
import { Vue, Options } from "vue-class-component";

@Options({
  props: {
    fieldName: {
      type: String,
      required: true,
    },
    placeholder: String,
    value: {
      default: "",
    },
  },
})
export default class FormItem extends Vue {
  onValueChanged(value: string) {
    this.$emit("update:value", value);
  }
}
